@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@300;500;700&display=swap);
html {
  margin: 0px;
  height: 100%;
  width: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0px;
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading {
  align-items: center;
  display: flex;
  justify-content: center;
  font-size: larger;
}


.login-page {
  background: #fafafa;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
}

.login-rectangle {
  align-content: center;
  align-self: center;
  display: flex;
  flex-direction: column;
}

.logo-trybe-tunes {
  align-self: center;
}

.form-rectangle {
  align-self: center;
  background: white;
  border-radius: 10px;
  box-shadow: 2px 4px 4px #b1b0b0;
  display: flex;
  flex-direction: column;
  height: 314px;
  justify-content: center;
  margin-top: 100px;
  width: 697px;
}

.login-form {
  align-self: center;
  display: flex;
  flex-direction: column;
}

.login-input {
  background: white;
  border: 1px solid #e1e5eb;
  font-size: 16px;
  margin-bottom: 30px;
  max-height: 50px;
  padding: 12px;
  width: 519px;
}

.btn {
  align-items: center;
  background: #003be5;
  border: none;
  border-radius: 2px;
  box-shadow: 1px 4px 4px rgba(0, 0, 0, 0.25);
  color: white;
  display: flex;
  flex-direction: row;
  font-size: 1rem;
  height: 40px;
  justify-content: center;
  padding: 8px 32px;
}

.header {
  display: flex;
  flex-direction: column;
  font-family: Epilogue , sans-serif;
}

.top-header {
  align-items: center;
  background: #023031;
  display: flex;
  flex-direction: row;
  height: 100px;
  justify-content: space-between;
}

.logo-header {
  height: 96px;
  margin-left: 35px;
  margin-top: -30px;
  width: 120px;
}

.user-button {
  align-items: center;
  background: white;
  border-radius: 50px;
  box-shadow: 1px 4px 4px rgba(0, 0, 0, 0.25);
  display: flex;
  font-weight: 500;
  height: 41.65px;
  justify-content: center;
  margin-right: 35px;
  width: 198.7px;
}

.navigation {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.link-nav {
  align-items: center;
  background: #f0f2f5;
  color: #2fc18c;
  display: flex;
  font-size: 28px;
  font-weight: 700;
  height: 78px;
  justify-content: center;
  line-height: 29px;
  text-decoration: none;
  width: 1439px;
}

.link-nav:hover {
  background: #2fc18c;
  color: white;
}

.favorites-nav {
  margin-left: 5px;
  margin-right: 5px;
}

.album-card {
  background: #f0f2f5;
  border: 1px solid #e1e5eb;
  border-radius: 6px;
  box-shadow: 1px 1px 2px black rgb(0.6) , 0.1px 1px 3px black rgba(0.2);
  display: flex;
  flex-direction: column;
  height: 250px;
  margin: 0 25px 25px;
  padding-bottom: 16px;
  width: 220px;
}

.album-image {
  display: flex;
  height: 180px;
}

.album-image img {
  border-radius: 6px;
  flex-grow: 1;
  padding: 7px;
}

.artist-info {
  display: flex;
  flex-direction: column;
  font-family: Verdana , Geneva , Tahoma , sans-serif;
  margin-left: 10px;
}

.artist-info span {
  color: #333d4d;
  font-size: 16px;
  font-weight: 700;
}

.artist-info p {
  color: #3d495c;
  font-weight: 400;
}

.search-page {
  display: flex;
  flex-direction: column;

}

.form-search {
  align-self: center;
  display: flex;
  justify-content: center;
  padding: 99px 0 112px;
}

.input-search {
  align-items: flex-start;
  border: 1px solid #3d495c;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  margin-right: 27px;
  padding: 12px 16px;
  width: 419px;
}

.search-button {
  align-items: center;
  background: #003be5;
  border-radius: 2px;
  color: white;
  display: flex;
  flex-direction: row;
  font-family: Verdana , Geneva , Tahoma , sans-serif;
  font-size: 16px;
  justify-content: center;
  padding: 8px 32px;
  width: 134px;
}

.album-list {
  align-self: center;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 40px 50px;
  max-width: 1370px;
}

.link-album-card {
  text-decoration: none;
}

.span-albuns-found {
  color: #001813;
  font-family: Epilogue , sans-serif;
  font-size: 24px;
  font-weight: 300;
  margin-left: 10%;
}

.div-not-found {
  align-self: center;
  color: #023031;
  font-family: Epilogue , sans-serif;
  font-size: 60px;
  font-weight: 300;
  margin-top: 200px;
}

.music-page {
  display: flex;
  justify-content: center;
  margin: 161px 50px;
}

.music-page .album-card {
  height: 280px;
  justify-content: space-between;
  margin-right: 100px;
  width: 270px;
}

.track-list-section {
  height: 25rem;
  overflow: scroll;
  width: 50rem;
}

::-webkit-scrollbar {
  height: 30px;
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background: #036b52;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #023031;
}

.track-list {
  display: flex;
  flex-direction: column;
}

.track-section {
  align-items: center;
  border-top: 1px solid #e1e5eb;
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

.track-name {
  margin-right: 10px;
}

.track-list-section {
    display: flex;
    justify-content: center;
    width: 90%;
    height: 100vh;
}

.track-list {
    /* align-self: center; */
    /* justify-self: center; */
    /* margin-top: 10rem; */
}
.profile-page {
  display: flex;
  flex-direction: column;
  background: red;
  height: 400px;
  width: 300px;
  align-items: center;
  /* justify-content: space-evenly; */
  margin: auto;
  margin-top: 100px;
}

.user-info-header {
  display: flex;
  align-items: center;
  background: black;
  width: 300px;
  justify-content: space-between;
}

